<template>
  <div class="app" :class="{'timer-running': isTimerRunning}">
    <Login
      v-if="!this.currentPlayer"
      @login="onLogin($event)"
      :players="players"
      class="section"
    />
      <div
        v-else
        class="section"
      >
      <WordGenerator
        class="generator"
        @select="isTimerRunning = true"
        :isTimerRunning="isTimerRunning"
        :currentPlayer="currentPlayer"
        :isFinished="isFinished"
      />
      <Timer
        class="timer"
        v-if="isTimerRunning"
        @finish="onFinish"
      />
    </div>
    <div class="animation">
      <Background />
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Login from '@/components/Login'
import WordGenerator from '@/components/WordGenerator'
import Timer from '@/components/Timer'
import Background from '@/components/Background'

export default {
  components: {
    Login,
    WordGenerator,
    Timer,
    Background
  },
  data() {
    return {
      teams: [],
      email: '',
      isLoggedIn: false,
      isTimerRunning: false,
      isFinished: false
    }
  },
  computed: {
    players() {
      let players = [];
      this.teams.forEach(team => {
        players = [...players, ...team.players]
      });
      return players;
    },
    currentPlayer() {
      return this.players.find(player => player.email === this.email);
    }
  },
  methods: {
    onLogin(email) {
      this.email = email;
    },
    getData() {
      const url = '/api/teams/';
      //const url = '/teams.json';
      axios.get(url).then(response => {
        this.teams = response.data;
      })
    },
    onFinish() {
      this.isTimerRunning = false;
      this.isFinished = true;
    }
  },
  created() {
    this.getData();
    setInterval(this.getData, 3000);
  }
}
</script>

